html {
  font-size: 62.5%;
  @media screen and (max-width: 995px) {
    font-size: 73.5%;
  }
  @media screen and (max-width: 767px) {
    font-size: 75.5%;
  }
}

body {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

:focus {
  outline: none;
}

button {
  cursor: pointer;
}

img {
  display: block;
}
