.p-title {
  background-color: #d94e09;
  font-family: "MidaShi-MB31";
  width: 51.7rem;
  height: 8.5rem;
  border-radius: 1rem;
  margin-left: 21.6rem;
  margin-top: 2.9rem;
  display: flex;
  align-items: center;
  box-shadow: 1px 1px rgba(grey, 0.5);
  @media screen and (max-width: 995px) {
    margin-left: 0;
    margin: 0 auto;
    margin-top: 2.9rem;
  }

  @media screen and (max-width: 767px) {
    width: 100%;
    margin-left: 0;
    margin: 0 auto;
    margin-top: 2.9rem;
  }

  @media screen and (max-width: 753px) {
    justify-content: center;
  }
  @media screen and (max-width: 630px) {
    justify-content: space-around;
    flex-direction: column;
    height: 300px;
  }
  span:nth-of-type(1) {
    align-self: flex-start;
    text-align: center;
    display: inline-block;
    background-color: #ffffff;
    font-size: 2.441rem;
    margin-top: 1.2rem;
    margin-left: 6.8rem;
    border-radius: 50%;
    width: 6.07rem;
    height: 6.05rem;
    line-height: 5.8rem;
    position: relative;

    @media screen and (max-width: 995px) {
      margin-top: 1.1rem;
    }

    @media screen and (max-width: 767px) {
      align-self: center;
      margin-top: 0;
    }
    @media screen and (max-width: 753px) {
      margin-left: 0;
    }

    &::before {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: inline-block;
      content: "";
      width: 6.7rem;
      height: 6.7rem;
      border-radius: 50%;
      border: 2px solid #ffffff;
    }
  }

  span:nth-of-type(2),
  span:nth-of-type(3) {
    font-size: 3rem;
    color: #ffffff;
    line-height: 1;
  }

  span:nth-of-type(2) {
    margin-left: 2rem;
    margin-top: -0.8rem;
    letter-spacing: 0.3px;
  }

  span:nth-of-type(3) {
    margin-left: -4px;
    margin-top: -8px;
    letter-spacing: -4.3px;
  }

  span:nth-of-type(4) {
    font-size: 1.647rem;
    color: #ffffff;
    margin-left: 3.4rem;
    margin-top: -3px;

    @media screen and (max-width: 753px) {
      margin-left: 1.4rem;
    }
  }
}
