/* layout
------------------------------------------------------------*/
body {
  display: grid;
  grid-template-columns: 1fr 95.9rem 1fr;
  grid-template-rows: 1fr;

  @media screen and (max-width: 995px) {
    grid-template-columns: 1fr 80vw 1fr;
    grid-template-rows: 1fr;
  }
}

.container {
  grid-row: 1 /1;
  grid-column: 2 / 3;
}
