.p-body {
  padding-top: 9.4rem;
  padding-bottom: 1rem;
  h1 {
    font-family: "MidaShi-MB31";
    color: #444444;
    font-size: 3rem;
    line-height: 1;
    margin-bottom: 1.4rem;
  }
  hr:nth-of-type(1) {
    border: 0;
    border-bottom: 1px solid #000000;
    margin-bottom: 2.2rem;
  }

  h2 {
    font-family: "Kakugothic-W6";
    color: #d94e09;
    font-size: 2rem;
    margin-bottom: 1.9rem;
    span {
      color: #888888;
    }
  }

  &__group {
    display: flex;
    column-gap: 1.9rem;
    margin-bottom: 2.3rem;

    @media screen and (max-width: 995px) {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr;
    }
  }

  &__left {
    p {
      font-family: "Kakugothic-W3";
      font-size: 1.4rem;
      max-width: 56rem;
      line-height: 2.4rem;
      margin-bottom: 2.3rem;
      font-weight: bold;

      @media screen and(max-width: 995px) {
        max-width: 100%;
      }
    }

    ul {
      background-color: #f3f3f3;
      list-style: none;
      padding-left: 1.4rem;
      padding-top: 0.7rem;
      margin-right: 4px;
      padding-bottom: 6px;

      @media screen and (max-width: 630px) {
        padding: 1rem;
      }

      li {
        font-family: "Kozukagothic-W3";
        font-size: 1.6rem;
        color: #000000;
        font-weight: bold;

        &:not(:first-child) {
          margin-top: -2px;
        }
      }

      li:nth-of-type(1) {
        &::before {
          display: inline-block;
          content: "";
          width: 1rem;
          height: 1rem;
          border-radius: 50%;
          background-color: #d94e09;
          margin-right: 7px;
        }
        font-family: "Kozukagothic-W6";
        color: #333333;
        font-size: 1.6rem;
        letter-spacing: -1px;
        font-weight: bold;
      }
      li:nth-of-type(2) {
        span {
          margin-right: 7px;
        }
      }
      li:nth-of-type(3) {
        letter-spacing: 0.1px;
        span {
          margin-right: 5px;
        }
      }
      li:nth-of-type(4) {
        span {
          margin-right: 8px;
        }
      }
    }
  }

  &__right {
    margin-top: -1px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @media screen and (max-width: 995px) {
      grid-row: 1 / 2;
      grid-column: 1 / -1;
      margin-bottom: 2rem;

      img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
      }
    }
  }

  & > p {
    font-family: "Kakugothic-W3";
    font-size: 1.4rem;
    line-height: 1;
    color: #000000;
    margin-left: -1px;
    margin-bottom: 2.9rem;
    font-weight: bold;
    @media screen and (max-width: 630px) {
      line-height: 2;
    }
  }

  hr:nth-of-type(2) {
    border: 0;
    border-bottom: 1px solid #dddddd;
  }
}
